html {
  font-size: 16px; // I would recommend always using 16px as base font-size
  @media (max-width: 1024px) {
    font-size: 14px;
  }
  @media (max-width: 640px) {
    font-size: 12px;
  }
}


body{
overflow-x: hidden;
  background:#222;
}

.preload {display:none;}

.App {margin:0;
padding:0;}


#window{
  margin:0;
  padding:0;
width: 100vw;
height: 100vh;
position:relative;
transition: background 2s;
background: url('./pand-min.jpg');
background-size: cover;
opacity:0.6;
}


#wrapper {
background-color:#222;
 color:cyan; }

#hi1{
color: cyan;
position:absolute;
top: -6rem;
left:25rem;
font-family: Gochi Hand;
width:50rem;
font-size:10rem;
transition: opacity 5s;
transition: color 5s;
}

#hi2{
position:absolute;
top: -6rem;
left:25rem;
font-family: Gochi Hand;
width:50rem;
font-size:10rem;
opacity:0;
transition: opacity 10s;
transition: color 10s;
}


input {
  position: relative;
  display: none;
}

label {
  display: block;
  height:50px;
  width:50px;
  top:3rem;
  left:1rem;
  position: fixed;
  cursor: pointer;
  background: url('./moon.png');
  background-size:cover;
  z-index:99;
}

input:checked + label {
  background: url('./smileysun.png');
 background-size:cover; 
 transition: background 1s;
}


input:checked ~ #window {
  background-image: url('./panb-min.jpg');
}



input:checked ~ #window > #hi1  {
  
  opacity: 0;
  transition: opacity 5s
}

input:checked ~ #window > #hi2  {
  
  color: #333;
  opacity: 1;
  font-family: "Gochi Hand", "Comic Sans", cursive;
  transition: opacity 5s;
  z-index:50;
}

input:checked ~ .arrow > span {
  border-bottom: 5px solid white;
    border-right: 5px solid white;
color: white;}

input:checked ~ #wrapper {
  color: #333;
  background-color:#F7F9FE;
}

